import React from "react";
import { connect } from "react-redux";
import {
  fetchProducts,
  addToWhistlist,
  addToCart
} from "../redux/actions/productActions";
import Products from "./products";

class ProductList extends React.Component {
  state = {
    productsList: [],
    lastCount: 6,
    filterProducts: [],
    showBtn: true
  };
  componentDidMount() {
    this.props.fetchProducts();
  }
  componentWillReceiveProps(nextProps) {
    if (this.props.products !== nextProps.products) {
      this.setState({
        showBtn:
          nextProps.products.arrayOfProducts.length < this.state.lastCount
            ? false
            : true,
        filterProducts: [...nextProps.products.arrayOfProducts],
        productsList: [...nextProps.products.arrayOfProducts]
      });
    }
  }
  addToWhistlist = id => {
    console.log(id);
  };
  renderProducts = e => {
    e.preventDefault();
    if (this.state.lastCount <= this.state.filterProducts.length) {
      this.setState({ lastCount: this.state.lastCount + 6 }, () => {
        this.setState({
          showBtn:
            this.state.lastCount >= this.state.filterProducts.length
              ? false
              : true
        });
      });
    } else {
      this.setState({ showBtn: false });
    }
  };
  sort = data => {
    switch (data) {
      case "l_to_h":
        let lowToHighProducts = this.state.filterProducts.sort((a, b) => {
          return a.price - b.price;
        });
        this.setState({ filterProducts: lowToHighProducts });
        break;
      case "h_to_l":
        let highToLowProducts = this.state.filterProducts.sort((a, b) => {
          return b.price - a.price;
        });
        this.setState({ filterProducts: highToLowProducts });
        break;
      case "category":
        let sortedProductsCategory = this.state.filterProducts.sort((a, b) => {
          a = a.category.toLowerCase();
          b = b.category.toLowerCase();
          return a < b ? -1 : a > b ? 1 : 0;
        });
        this.setState({ filterProducts: sortedProductsCategory });
        break;
      case "Default":
        console.log(data);
        this.setState({ filterProducts: [...this.state.productsList] });
        break;
    }
  };
  onCategoryChange = cat => {
    let products =
      cat === "all"
        ? [...this.state.productsList]
        : this.state.productsList.filter(product => {
            return product.category === cat;
          });
    this.setState({ filterProducts: products, lastCount: 6 }, () => {
      this.setState({
        showBtn:
          this.state.lastCount >= this.state.filterProducts.length
            ? false
            : true
      });
    });
  };
  render() {
    const { whistlist, addToCart } = this.props;
    return (
      <Products
        {...this.props}
        addToCart={addToCart}
        whistlist={whistlist}
        sort={this.sort}
        addToWhistlist={this.props.addToWhistlist}
        showBtn={this.state.showBtn}
        onCategoryChange={this.onCategoryChange}
        products={this.state.filterProducts.slice(0, this.state.lastCount)}
        renderProducts={this.renderProducts}
      />
    );
  }
}
const mapStateToProps = state => {
  return {
    products: state.products,
    whistList: state.whistlist
  };
};
export default connect(mapStateToProps, {
  fetchProducts,
  addToWhistlist,
  addToCart
})(ProductList);
