import React from 'react'
import { connect } from "react-redux";
import {productCounter} from '../redux/actions/productActions'
function CartItem({id,name,price,category,quantity,imgUrl,total,productCounter}) {
    return (
        <div>
            <li className="cart_item item_list d-flex flex-lg-row flex-column align-items-lg-center align-items-start justify-content-lg-end justify-content-start">
                <div className="product d-flex flex-lg-row flex-column align-items-lg-center align-items-start justify-content-start mr-auto">
                    <div><div className="product_number">1</div></div>
                    <div><div className="product_image"><img src={imgUrl} alt=""/></div></div>
                    <div className="product_name_container">
                        <div className="product_name"><a href="product.html">{name}</a></div>
                        <div className="product_text">Second line for additional info</div>
                    </div>
                </div>
                <div className="product_color product_text"><span>Category: </span>{category}</div>
                <div className="product_size product_text"><span>Quantity: </span>{quantity}</div>
                <div className="product_price product_text"><span>Price: </span>${price}</div>
                <div className="product_quantity_container">
                    <div className="product_quantity ml-lg-auto mr-lg-auto text-center">
                        <span className="product_text product_num">{quantity}</span>
                        <div disabled={quantity ===0 ? true:false} onClick={()=>{productCounter(id,'minus')}} className="qty_sub qty_button trans_200 text-center"><span>-</span></div>
                        <div onClick={()=>{productCounter(id,'plus')}} className="qty_add qty_button trans_200 text-center"><span>+</span></div>
                    </div>
                </div>
                <div className="product_total product_text"><span>Total: </span>${total}</div>
            </li>
        </div>
    )
}
export default connect(null,{productCounter})(CartItem)