import Axios from "axios";

export const fetchProducts = ()=>{
    return (dispatch) =>{
        Axios.get('./data/products.json')
        .then(product=>{
            dispatch({
                type:'fetch_products',
                payload:product.data
            })
        })
    }
}
export const addToWhistlist = (id)=>{
    return (dispatch,getState)=>{
        const state = getState()
        if(state.whistlist.some(item=>item.id===id)) {
            let itemToRemove = state.whistlist.find(item=>{
                return item.id === id;
            })
            let filteredWhistList = state.whistlist.filter(item=>{
                return item.id !== itemToRemove.id;
            })
            dispatch({
                type:'REMOVE_FROM_WHISTLIST',
                payload:filteredWhistList
            })
        }
        else {
            let newItem = state.products.arrayOfProducts.find(item=>{
                return item.id === id;
            })
            dispatch({
                type:'ADD_TO_WHISTLIST',
                payload:newItem
            })
        }
    }
}
export const addToCart = (id,name)=>{
    return (dispatch,getState)=>{
        dispatch({
            type:'CURRENT_PRODUCT',
            payload:name
        })
        const state = getState();
        let product = state.products.arrayOfProducts.find(item=>id==item.id)
        let oldCart = [...state.cart];
        if(oldCart.some(item=>item.id == product.id)) {
            const index = oldCart.findIndex(item=>{
                return item.id == product.id
            })
            let updatedCart = [...oldCart];
            updatedCart[index] = {...product,quantity:updatedCart[index].quantity+1,total:product.price*(updatedCart[index].quantity+1)}
            dispatch({
                type:'ADD_TO_CART',
                payload:updatedCart
            })
        }
        else {
            let updatedCart = [...oldCart,{...product,quantity:1,total:product.price}]
            dispatch({
                type:'ADD_TO_CART',
                payload:updatedCart
            })
        }
        let cart = [...getState().cart];
        let cartProperties = {
            cartCount:0,
            cartTotal:0
        }
        cartProperties.cartCount = cart.length > 0 ? cart.reduce((total=0,item)=>{
           return total + item.quantity
        },0) : 0
        cartProperties.cartTotal = cart.length > 0 ? cart.reduce((total=0,item)=>{
            return total + item.total
         },0) : 0
        dispatch({
            type:'CART_COUNT',
            payload:cartProperties
        })

    }
}
export const productCounter = (id,type)=>{
    return (dispatch,getState)=>{
        let cart =  [...getState().cart];
        let updatedCart = cart.map(item=>{
            return item.id === id ? 
            {...item,quantity:type==='plus'?item.quantity+1:item.quantity-1,total:type==='plus'?(item.quantity+1)*item.price:(item.quantity-1)*item.price}:item
        })
        dispatch({
            type:'PRODUCT_COUNT_PLUS',
            payload:updatedCart
        })
        let cartProperties = {
            cartCount:0,
            cartTotal:0,
            cart:[]
        }
        let newCart =  [...getState().cart];
        cartProperties.cart = newCart.filter(item=>{
            return item.quantity !== 0
        })
        cartProperties.cartTotal = newCart.reduce((total,item)=>{
            return total + item.total
        },0)
        cartProperties.cartCount = newCart.reduce((total,item)=>{
            return total + item.quantity
        },0)
        dispatch({
            type:'UPDATE_TOTAL',
            payload:cartProperties
        })
    }
}
