export const clearCart = () =>{
    return (dispatch,getState)=>{
        let updatedCart = []
        dispatch({
            type:'CLEAR_CART',
            payload:updatedCart
        })
    }
}
export const clearAlert = () => ({type:'CLEAR_ALERT'})
