import React from "react";
const Product = props => {
  const {
    addToCart,
    src,
    price,
    category,
    name,
    id,
    addToWhistlist,
    active
  } = props;

  return (
    <div className="col-xl-4 col-md-6">
      <div className="product">
        <div className="product_image">
          <img src={src} alt="shoping app" />
        </div>
        <div className="product_content">
          <div className="product_info d-flex flex-row align-items-start justify-content-start">
            <div>
              <div>
                <div className="product_name">
                  <a href="product.html">{name}</a>
                </div>
                <div className="product_category">
                  In <a href="category.html">{category}</a>
                </div>
              </div>
            </div>
            <div className="ml-auto text-right">
              <div className="rating_r rating_r_4 home_item_rating">
                <i></i>
                <i></i>
                <i></i>
                <i></i>
                <i></i>
              </div>
              <div className="product_price text-right">
                ${price}
                <span>.99</span>
              </div>
            </div>
          </div>
          <div className="product_buttons">
            <div className="text-right d-flex flex-row align-items-start justify-content-start">
              <div
                onClick={() => {
                  addToWhistlist(id);
                }}
                className={
                  active
                    ? "product_button product_fav text-center d-flex flex-column align-items-center justify-content-center active"
                    : "product_button product_fav text-center d-flex flex-column align-items-center justify-content-center"
                }
              >
                <div>
                  <div>
                    <img
                      alt="shoping app"
                      src={require("./images/heart_2.svg")}
                    />
                    <div>+</div>
                  </div>
                </div>
              </div>
              <div
                onClick={() => {
                  addToCart(id, name);
                }}
                className="product_button product_cart text-center d-flex flex-column align-items-center justify-content-center"
              >
                <div>
                  <div>
                    <img alt="shoping app" src={require("./images/cart.svg")} />
                    <div>+</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Product;
