import React from "react";
import { connect } from "react-redux";
import Product from "./product";
import { Link } from "react-router-dom";
import { clearAlert } from "../redux/actions/productActions";
const _Products = props => {
  const {
    clearAlert,
    current_product,
    alert,
    addToCart,
    products,
    renderProducts,
    onCategoryChange,
    showBtn,
    sort,
    addToWhistlist,
    whistList
  } = props;
  function inList(id) {
    if (whistList) {
      return whistList.some(item => item.id === id);
    } else {
      return null;
    }
  }
  //let inList = whistList? whistList.some(item=>item.id==id) : false
  const productsList =
    products &&
    products.map(product => {
      return (
        <Product
          productItem={product}
          addToCart={addToCart}
          active={inList(product.id)}
          id={product.id}
          addToWhistlist={addToWhistlist}
          name={product.name}
          category={product.category}
          key={product.id}
          src={product.imgUrl}
          price={product.price}
        />
      );
    });

  return (
    <div className="products">
      {alert && (
        <div className="alert alert-success my-alert">
          {current_product} has been added to your{" "}
          <Link to="/shoping-app/cart">cart</Link>
          <i onClick={clearAlert} className="fa fa-close"></i>
        </div>
      )}
      <div className="products">
        <div className="container">
          <div className="row">
            <div className="col-lg-6 offset-lg-3">
              <div className="section_title text-center">
                Popular on Little Closet
              </div>
            </div>
          </div>
          <div className="row page_nav_row">
            <div className="col">
              <div className="sorting">
                <h3>Sort</h3>
                <select
                  className="form-control"
                  onChange={e => {
                    sort(e.target.value);
                  }}
                >
                  <option defaultValue>Default</option>
                  <option value="l_to_h">Price Low To High</option>
                  <option value="h_to_l">Price High To Low</option>
                  <option value="category">Category</option>
                </select>
              </div>
              <div className="page_nav">
                <ul className="d-flex flex-row align-items-start justify-content-center">
                  <li className="active">
                    <a
                      onClick={() => {
                        onCategoryChange("all");
                      }}
                    >
                      All
                    </a>
                  </li>
                  <li>
                    <a
                      onClick={() => {
                        onCategoryChange("combo");
                      }}
                    >
                      Combo
                    </a>
                  </li>
                  <li>
                    <a
                      onClick={() => {
                        onCategoryChange("shirts");
                      }}
                    >
                      Shirts
                    </a>
                  </li>
                  <li>
                    <a
                      onClick={() => {
                        onCategoryChange("jeans");
                      }}
                    >
                      Jeans
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div className="row products_row">{productsList}</div>
          <div className="row load_more_row">
            <div className="col">
              {showBtn && (
                <div className="button load_more ml-auto mr-auto">
                  <a
                    onClick={e => {
                      renderProducts(e);
                    }}
                  >
                    load more
                  </a>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const Products = connect(
  state => {
    return {
      whistList: state.whistlist,
      alert: state.alert,
      current_product: state.current_product
    };
  },
  { clearAlert }
)(_Products);
export default Products;
