import React from 'react'
import {connect} from 'react-redux';
import { BrowserRouter as Router ,NavLink,Link} from "react-router-dom";

const _Header = (props)=>{
    return(
		<React.Fragment>
        <header className="header">
			<div className="header_overlay"></div>
			<div className="header_content d-flex flex-row align-items-center justify-content-start">
				<div className="logo">
					<Link to="/" exact="true">
						<div className="d-flex flex-row align-items-center justify-content-start">
							<div><img src={require('./images/logo_1.png')} /></div>
							<div>Little Closet</div>
							
						</div>
					</Link>	
				</div>
				<div className="header_right d-flex flex-row align-items-center justify-content-start ml-auto">
					<div className="header_search">
						<form action="#" id="header_search_form">
							<input type="text" className="search_input" placeholder="Search Item" required="required"></input>  
							<button className="header_search_button"><img src={require('./images/search.png')} /></button>
						</form>
					</div>
					<div className="user"><a href="#"><div><img src={require('./images/heart_2.svg')} /><div>{props.whistlist}</div></div></a></div>
					<div className="cart"><Link to="/cart"><div><img src={require('./images/cart.svg')} /><div>{props.cart}</div></div></Link></div>
					<div className="header_phone d-flex flex-row align-items-center justify-content-start">
						<div><div><img src={require('./images/phone.svg')} /></div></div>
						<div>+1 912-252-7350</div>
					</div>
				</div>
			</div>
		</header>
			
		</React.Fragment>
    )
}

const Header = connect(
    state => {
        return {
			whistlist:state.whistlist.length,
			cart:state.cartCount
        }
    }
  )(_Header);
export default Header