const initialState = {
  products: [],
  cart: [],
  cartCount: 0,
  whistlist: [],
  alert: false,
  current_product: "",
  total: 0
};
const productReducer = (state = initialState, action) => {
  switch (action.type) {
    case "fetch_products":
      let newProducts = action.payload;
      let newState = { ...state, products: newProducts };
      return newState;
    case "ADD_TO_WHISTLIST":
      let updatedWhistList = [...state.whistlist, action.payload];
      return (state = { ...state, whistlist: updatedWhistList });
    case "REMOVE_FROM_WHISTLIST":
      return (state = { ...state, whistlist: action.payload });
    case "ADD_TO_CART":
      return (state = { ...state, cart: action.payload, alert: true });
    case "CURRENT_PRODUCT":
      return (state = { ...state, current_product: action.payload });
    case "CART_COUNT":
      return (state = {
        ...state,
        cartCount: action.payload.cartCount,
        total: action.payload.cartTotal
      });
    case "PRODUCT_COUNT_PLUS":
      return (state = { ...state, cart: action.payload });
    case "UPDATE_TOTAL":
      return (state = {
        ...state,
        cartCount: action.payload.cartCount,
        total: action.payload.cartTotal,
        cart: action.payload.cart
      });
    case "CLEAR_CART":
      return (state = {
        ...state,
        cart: action.payload,
        cartCount: 0,
        total: 0
      });
    case "CLEAR_ALERT":
      return (state = { ...state, alert: false });
    default:
      return state;
  }
};
export default productReducer;
